.blog-container{
  display: flex;
  flex-direction: column;
  
  height: 100%;

  h1 {
    color:#D23EFF;
    font-size: 36pt;
    text-decoration: underline;
  }

  .blog-posts{
    flex:1;
    justify-content: center;
    text-decoration: underline;
    color:greenyellow;
    width:100%;
    
    #post {
      display:flex;
      flex-direction: row;
    h2 {
      flex:1;
     
      align-self: center;
      cursor: pointer;
    }
    img {
      flex:1;
      margin:25px 0 25px 0;
    }
  }
  }
}